html {
  box-sizing: border-box; }

*, *::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Georgia", serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #666666; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  position: relative;
  max-width: 1919px;
  margin: 0 auto; }

@media (max-width: 1919px) {
  .container {
    min-width: 767px; } }

@media (max-width: 767px) {
  .container {
    min-width: 320px;
    width: 100%; } }

.header__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 32px;
  padding: 0 13.255%;
  z-index: 1; }

.header__logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  outline: none;
  cursor: pointer; }
  .header__logo:hover, .header__logo:focus {
    opacity: 0.5; }
  .header__logo:active {
    opacity: 0.8; }

.header__logo img {
  margin-right: 15px; }

.header__logo div {
  max-width: 300px;
  max-height: 60px;
  padding-top: 8px;
  overflow: hidden;
  text-overflow: ellipsis; }

.header__logo p {
  margin: 0 0 5px 0;
  padding: 0;
  max-height: 27px;
  font-family: "Roboto Condensed", "Aria", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 27px;
  color: #FFFFFF;
  overflow: hidden;
  text-overflow: ellipsis; }

.header__logo span {
  display: inline-block;
  vertical-align: center;
  max-height: 14px;
  font-family: "Roboto", "Aria", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  color: #788891;
  overflow: hidden;
  text-overflow: ellipsis; }

.header__contact,
.header__contact ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-overflow: ellipsis; }

.header__contact {
  flex-wrap: wrap;
  justify-content: flex-end;
  flex-grow: 1;
  max-width: 1100px;
  max-height: 32px; }

.header__contact ul {
  flex-wrap: wrap;
  justify-content: flex-end;
  max-width: 850px;
  max-height: 20px;
  list-style: none; }

.header__contact li {
  max-width: 250px;
  max-height: 24px; }

.header__contact li:not(:last-of-type) {
  margin-right: 30px; }

.header__contact a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: "PT Mono", "Georgia", serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #FFFFFF;
  text-decoration: none;
  outline: none;
  cursor: pointer; }
  .header__contact a:hover, .header__contact a:focus {
    opacity: 0.5; }
  .header__contact a:active {
    opacity: 0.8; }

.header__contact a svg {
  margin-right: 20px; }

.header__contact ul ~ a {
  max-width: 200px;
  max-height: 16px;
  margin-left: 4.13%;
  padding: 15px 25px;
  font-family: "Roboto Condensed", "Aria", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  color: #FFFFFF;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  overflow: hidden;
  text-overflow: ellipsis; }

@media (max-width: 1919px) {
  .header__wrapper {
    margin-top: 20px;
    padding: 0 3.25%; }
  .header__contact {
    max-width: 500px; }
  .header__contact ul {
    max-height: 24px; }
  .header__contact a svg {
    margin-right: 0; }
  .header__contact a[href^="mailto:"] svg {
    width: 26px;
    height: 19px; }
  .header__contact a[href^="tel:"] svg {
    width: 24px;
    height: 24px; }
  .header__contact a span {
    display: none; }
  .header__contact ul ~ a {
    display: none; } }

@media (max-width: 767px) {
  .header__wrapper {
    margin-top: 17px;
    padding: 0 5%; }
  .header__logo div {
    display: none; }
  .header__contact {
    max-width: 200px; }
  .header__contact li:not(:last-of-type) {
    margin-right: 20px; } }

.intro {
  background-color: #1B2931;
  min-height: 100px; }
